.contact-us-container {
    margin: 1rem 1rem;
}

.contact-us-container .heading {
    font-weight: 600;
    line-height: 3.281rem;
    color: #555;
    font-size: 2.8rem;
    align-self: center;
    letter-spacing: .028rem;
}

/* .contact-us-container .row {
    padding: 1rem
} */

.contact-us-container i {
    padding: 1rem;
}

.contact-us-container .content {
    margin-left: 4rem;
    font-weight: 400;
    font-size: larger;
}

.contact-us-container .content-heading {
    font-size: 1.2rem;
    font-weight: 600;
}

.contact-us-container .fluid-container {
    width: 50%;
    margin: 2rem;
}

@media screen and (max-width:576px) {
    .contact-us-container .fluid-container {
        width: 100%;
        margin: 0;
    }
    .contact-us-container .fluid-container {
        margin-top: 2rem;
    }
/* 
    .contact-us-container .col-sm-12 {
        margin-bottom: 1rem;
    } */
}